.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.landing{
  height: 76vh;
  background: linear-gradient( to right, rgba(26, 80, 139, 1), rgba(117, 79, 254, 0.2) ), url(./img/umaukpai-1.jpg);
  /* background-image: linear-gradient(to bottom, #754ffe, #5729fe),url("./img/umaukpai-bg.jpg"); */
  background-position: center;
  background-size: cover;
}

.auth-bg{
  background: linear-gradient(90deg, rgba(2,0,36,0.8) 10%, rgba(9,9,121,0.8) 35%, rgba(0,212,255, 0.8) 100%), url(./img/umaukpai-2.jpg);
}
.logo{
  width: 5rem;
}
.msg{
  margin-top: 5rem;
}

.footer{
  background-color: #ffffff;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
